import { Component, ViewChild, Input, OnInit, ComponentFactoryResolver, ViewContainerRef } from '@angular/core';
import { NavigationService } from 'smartux-client';

@Component({
  selector: 'include-screen',
  template: `
        <div #vc></div>
    `
})

export class IncludeScreenComponent implements OnInit {

  @Input() name: string;
  @ViewChild('vc', {read: ViewContainerRef}) vc: ViewContainerRef;

  constructor(
    private navigationService: NavigationService,
    private componentFactoryResolver: ComponentFactoryResolver
  ) {
  }

  ngOnInit() {
    if (this.name) {
      this.renderComponent();
    }
  }

  renderComponent() {
    let defaultLayout = this.navigationService.appConfig.defaultLayout;
    let component = this.navigationService.appScreens[this.name][defaultLayout];
    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);
    this.vc.clear();
    let componentRef = this.vc.createComponent(componentFactory);
    componentRef.changeDetectorRef.detectChanges();
  }
}



